import { registerBlockType } from "@wordpress/blocks"

registerBlockType("voblocktheme/listitem", {
  title: "List Item",
  attributes: {
    icon: { type: "string" },
    title: { type: "string" },
    body: { type: "string" }
  },
  edit: EditComponent,
  save: SaveComponent
})

function EditComponent(props) {
  function updateIcon(e) {
    props.setAttributes({ icon: e.target.value })
  }

  function updateTitle(e) {
    props.setAttributes({ title: e.target.value })
  }

  function updateBody(e) {
    props.setAttributes({ body: e.target.value })
  }

  return (
    <div className="welcome-list mobile-accrdn mobile-accrdn-first">
      <div className="row">
        <input type="text" placeholder="icon class" value={props.attributes.icon} onChange={updateIcon} />
        <input type="text" placeholder="title" value={props.attributes.title} onChange={updateTitle} />
        <textarea placeholder="body text" value={props.attributes.body} onChange={updateBody} />
      </div>
    </div>
  );
}

function SaveComponent() {
  return null
}